.btn {
    @apply .p-4 .font-medium .text-lg .text-gray-200 .shadow .transition-all .duration-300 .ease-in-out;
}

.btn:disabled {
    @apply .opacity-50 .cursor-not-allowed;
}

.btn-teal {
    @apply .bg-teal-500;
}

.btn-teal:hover:not([disabled]) {
    @apply .bg-teal-400;
}

.btn-blue {
    @apply .bg-blue-500;
}

.btn-blue:hover:not([disabled]) {
    @apply .bg-blue-400;
}

.btn-spinner,
.btn-spinner:after {
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
}

.btn-spinner {
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: .2em solid white;
    border-right: .2em solid white;
    border-bottom: .2em solid white;
    border-left: .2em solid transparent;
    transform: translateZ(0);
    animation: spinning 1s infinite linear;
}

@keyframes spinning {
    0% { transform: rotate(0deg) }
    100% { transform: rotate(360deg) }
}
